@use '../../styles/variables' as *;
@use '../../styles/mixins' as mixins;

.sidebar {
  position: sticky;
  top: 1rem;
  margin-top: 2rem;
  right: 0;
  max-width: 22rem;
  width: 100%;
  background-color: $GREY50;
  overflow: hidden;
  transition: max-width 0.3s, padding 0.3s;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  height: fit-content;
  border-radius: 1rem;
  margin-right: 1rem;

  &.open {
    max-width: 22rem;
    padding: 1rem 1rem 3rem 1rem;
  }

  &.closed {
    max-width: 5rem;
    padding: 0.5rem;
  }
}

.toggleButton {
  background-color: $GREY300;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  padding: 0.8rem;
  transition: background-color 0.3s;

  &:hover {
    background-color: $GREY400;
  }

  svg {
    font-size: $font-md;
    color: $dark-grey;
  }
}

.item {
  border-radius: 0.6rem;
  margin-bottom: 0.5rem;
  transition: background-color 0.3s;
  // box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  // border-bottom: 1px solid $MAIN500;

  // &.active {
  //   background-color: $GREY300;

  //   &:hover {
  //     background-color: $GREY400;
  //   }
  // }

  // &:hover {
  //   background-color: $GREY300;
  // }

  &:last-child {
    margin-bottom: 0;
  }
}

