.link {
  --color: #fedadf;
  background-color: transparent;
  border-radius: 0.3em;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
  font-weight: 400;
  border: 1px solid $GREY300;
  color: var(--color);
  font-size: $font-md2;
  z-index: 1;
  color: $GREY900;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 0.75rem 1rem;
  width: 100%;
  white-space: nowrap;

  &.active {
    background-color: $MAIN100;
  }

  .icon {
    font-size: $font-md2;
    color: $MAIN900;
    transition: color 0.3s;
  }
}

.link::before,
.link::after {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%);
  position: absolute;
  border-radius: 50%;
  z-index: -1;
  background-color: var(--color);
  transition: 1s ease;
}

.link::before {
  top: -1em;
  left: -1em;
}

.link::after {
  left: calc(100% + 1em);
  top: calc(100% + 1em);
}

.link:hover::before,
.link:hover::after {
  height: 410px;
  width: 410px;
}

.link:hover {
  color: rgb(10, 25, 30);
}

.link:active {
  filter: brightness(0.8);
}
