@use '../../styles/variables' as *;
@use '../../styles/mixins' as mixins;

.navbar {
  width: 98.5%;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  margin: 1rem auto 0 auto;
  background-color: $GREY50;
  border-bottom: 1px solid $GREY400;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
    rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;

  border-radius: 3rem;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;

  .logo {
    display: flex;
    align-items: center;
    cursor: pointer;
    font-weight: 600;

    .logoIcon {
      margin-right: 1rem;
      color: $MAIN900;
      font-size: 2rem;
    }
  }

  .buttons {
    display: flex;
    align-items: center;

    .logoutButton {
      display: inline-block;
      padding: 0.5rem 2rem;
      font-weight: 700;
      color: $dark-grey;
      border: 1px solid $MAIN900;
      border-radius: 1rem;
      position: relative;
      background-color: transparent;
      overflow: hidden;
      z-index: 1;
      transition: all 0.3s ease;

      &:hover {
        color: $GREY100;
      }
    }

    .logoutButton::before {
      content: '';
      position: absolute;
      right: -120%;
      bottom: 0;
      width: 120%;
      height: 100%;
      background-color: $MAIN900;
      transition: all 0.3s;
      z-index: -1;
    }

    .logoutButton:hover::before {
      transform: translateX(-90%);
    }

    svg {
      color: $MAIN900;
    }
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 50%;
  max-width: 30rem;
  background-color: $white;
  box-shadow: -4px 0 6px rgba(0, 0, 0, 0.2);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 2000;
  display: flex;
  flex-direction: column;

  &.open {
    transform: translateX(0);
  }

  &.closed {
    transform: translateX(100%);
  }

  .modalHeader {
    display: flex;
    justify-content: flex-end;
    padding: 1rem;

    .closeIcon {
      cursor: pointer;
      color: $dark-grey;
    }
  }

  .menuList {
    display: flex;
    flex-direction: column;

    .menuItem {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 1rem;
      padding: 1rem;
      cursor: pointer;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: $GREY200;
      }

      span {
        font-size: $font-md2;
        font-weight: 500;
        color: $dark-grey;
      }

      svg {
        font-size: $font-md2;
        color: $MAIN900;
      }
    }
  }
}
